.order-all {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 45px;
  min-height: 289px;
  width: auto;
  background-color: #68b738;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-size: 280px auto;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) and (max-width: 1299px) {
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    background-size: 708px;
    min-height: 200px;
    padding-left: 422px;
    padding-right: 120px;
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    height: auto;
    background-image: url("../img/gift-photo-desktop@1x.jpg");
    background-size: cover;
    padding: 42px;
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");

    @media (min-width: $tablet-width) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }

    @media (min-width: $desktop-width) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }
}

.order-all::before {
  position: absolute;
  content: "";
  width: 55px;
  height: 60px;
  background-image: url("../img/icon-gift.svg");
  top: 65px;
  left: 50%;
  transform: translateX(-50%);

  @media (min-width: $tablet-width) and (max-width: 1299px) {
    left: 90px;
    transform: none;
  }
}

.order-all__link {
  display: block;
  margin-top: auto;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  padding-top: 90px;
  // padding-left: 55px;
  // padding-right: 55px;

  @media (min-width: $tablet-width) and (max-width: 1299px) {
    text-align: left;
    margin-bottom: auto;
    padding-top: 0;
  }
}
